.GameWrapper {
  border: .3em solid #333;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-left: 1em;
  margin-right: 1em;
  background-color: #f5f5f5;
  border-radius: 1em;
}

p{
  text-align: center;
}

a {
  text-align: center;
}

@media(width < 1000px) {
.GameWrapper {
  /*border:1px solid black;*/
  display: flex;
  flex-direction: column;
  margin-left: 0.5em;
  margin-right: 0.5em;
}
}




.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
